
html {
  height: 100%;
}
body {
  height: 100%;
  background: url(/static/img/background1.76d07e1.jpg);
  background-size:100%;
  background-repeat: no-repeat;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  text-align: center;
  color: black;
  margin: 0;
}
#app {
  margin: 0px;
  height: 100%;
}
.tabs {
  height: 10%;
  background-color: black;
}
.content {
  height: 90%;
  background-color: bisque;
}
ul {
  height:100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
li {
  height: 100%;
  margin-left: 2%;
  margin-right: 2%;
  list-style: none;
  border: 1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
a {
  height: 100%;
  color: whitesmoke;
  text-decoration: none;
  font-size: 2em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.opacity60 {
    filter: alpha(opacity : 60);
    opacity:0.6;
    -moz-opacity: 0.6;
    -khtml-opacity:0.6;
}
.opacity70 {
    filter: alpha(opacity : 70);
    opacity: 0.7;
    -moz-opacity: 0.7;
    -khtml-opacity: 0.7;
}
.opacity90 {
    filter: alpha(opacity : 90);
    opacity:0.9;
    -moz-opacity: 0.9;
    -khtml-opacity:0.9;
}

#name {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 5.5em;
}
#name span {
  cursor: pointer;
}

#list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: dimgray;
}
